@-webkit-keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(0.5);
  }
  100% {
    transform: scaleY(1);
  }
}
@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(0.5);
  }
  100% {
    transform: scaleY(1);
  }
}
@-webkit-keyframes growUp {
  0% {
    transform: scaleY(1);
  }
  80% {
    transform: scaleY(0.5);
  }
  100% {
    transform: scaleY(0);
  }
}
@keyframes growUp {
  0% {
    transform: scaleY(1);
  }
  80% {
    transform: scaleY(0.5);
  }
  100% {
    transform: scaleY(0);
  }
}
